import { useEffect } from 'react';
// material
import { Container, Grid, Alert, Stack, Button, Box } from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
// hooks
import useAuth from '../../hooks/useAuth';
import { handlePermission } from '../../utils/handlePermission';
import { PERMISSIONS } from '../../guards/Permissions';
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import { PATH_DASHBOARD } from '../../routes/paths';
import {
  AppAreaFiches,
  AppLotto,
  AppGain,
  AppGPC,
  AppTirage,
  AppTotalPerte,
  AppTotalFichesMontant,
  AppTotalCommission,
  AppTotalGain,
  AppTotalFiches,
  Tirage,
  LotGagnant,
  AppTotalAgentActif,
  AppTotalAnother,
  AppTotalAnother2
} from '../../components/_dashboard/general-app';
import { useDispatch, useSelector } from '../../redux/store';
import { getApp, getAppTirageLotgagnant } from '../../redux/slices/user';

// ----------------------------------------------------------------------

export default function GeneralApp() {
  const { themeStretch } = useSettings();
  const { user } = useAuth();

  const dispatch = useDispatch();
  const { app, appTirageLotgagnant } = useSelector((state) => state.user);
  const role = window.localStorage.getItem('role');

  useEffect(() => {
    dispatch(getApp());
    dispatch(getAppTirageLotgagnant());
  }, [dispatch]);

  return (
    <Page title="General: App | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Grid container spacing={2}>
          {user?.promotion_text?.map((item) => (
            <Grid item xs={12} md={12}>
              <Alert variant="filled" severity="info">
                {item}
              </Alert>
            </Grid>
          ))}
          {user?.status_payment === 'expired' && (
            <Grid item xs={12} md={12}>
              <Alert variant="filled" severity="error">
                {user?.status_payment !== 'expired'
                  ? `Compte expira dans ${user?.status_payment} jours`
                  : 'Compte expiré'}
              </Alert>
            </Grid>
          )}

          {user?.status_payment <= 10 && (
            <Grid item xs={12} md={12}>
              <Alert variant="filled" severity="error">
                {user?.status_payment !== 'expired'
                  ? `Compte expira dans ${user?.status_payment} jours`
                  : 'Compte expiré'}
              </Alert>
            </Grid>
          )}

          {handlePermission(user?.perms, PERMISSIONS.VIEW_APP_LOT_GAGNANT) &&
            appTirageLotgagnant?.map((item) => (
              <Grid item xs={12} md={12 / appTirageLotgagnant?.length}>
                <Tirage id={item.id} list={item.list} />
              </Grid>
            ))}

          {handlePermission(user?.perms, PERMISSIONS.VIEW_APP_REPORT) && user?.role !== 'superuser' && (
            <>
              <Grid item xs={12} md={2.4}>
                <AppTotalFichesMontant ficheMontant={app?.ficheMontant} ficheCount={app?.ficheCount} />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalPerte fichePerteMontant={app?.fichePerteMontant} />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalCommission commission={app?.commission} />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalAgentActif agentActifCount={app?.agentActifCount} agentInactifCount={app?.agentInactifCount} />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalGain ficheGainMontant={app?.ficheGainMontant} />
              </Grid>
            </>
          )}

          {handlePermission(user?.perms, PERMISSIONS.VIEW_APP_REPORT) && user?.role === 'superuser' && (
            <>
              <Grid item xs={12} md={3}>
                <AppTotalAnother fontSize={14} text="Total Fiches Generales" bgColor="red" value={app?.ficheCount} />
              </Grid>
              <Grid item xs={12} md={3}>
                <AppTotalAnother
                  fontSize={14}
                  text="Total Fiches Eliminées"
                  bgColor="#6F1AB6"
                  value={app?.ficheSupCount}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <AppTotalAnother
                  fontSize={14}
                  text="Total Fiches Gagnées"
                  bgColor="#ff9612"
                  value={app?.ficheWinCount}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <AppTotalAnother
                  fontSize={14}
                  text="Total Fiches Payées"
                  bgColor="#226597"
                  value={app?.fichePaidCount}
                />
              </Grid>

              <Grid item xs={12} md={3}>
                <AppTotalAnother fontSize={14} text="Total Compagnies" bgColor="#2192FF" value={app?.companyCount} />
              </Grid>
              <Grid item xs={12} md={3}>
                <AppTotalAnother
                  fontSize={14}
                  text="Total Agent prepayées"
                  bgColor="#6C00FF"
                  value={app?.agentPrepaidCount}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <AppTotalAnother fontSize={14} text="Pos Actifs" bgColor="#128061" value={app?.agentActifCount} />
              </Grid>

              <Grid item xs={12} md={3}>
                <AppTotalAnother fontSize={14} text="Pos Inactifs" bgColor="#F95959" value={app?.agentInactifCount} />
              </Grid>
            </>
          )}

          {handlePermission(user?.perms, PERMISSIONS.VIEW_REPORT_PARTIEL) && (
            <Grid item xs={12} md={12}>
              <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <Button
                  component={RouterLink}
                  variant="contained"
                  size="large"
                  sx={{ px: 3 }}
                  to={PATH_DASHBOARD?.rapport?.journalier}
                >
                  Rapport journalier
                </Button>
              </Box>
            </Grid>
          )}

          {handlePermission(user?.perms, PERMISSIONS.VIEW_LOT_GAGNANT_PARTIEL) && (
            <Grid item xs={12} md={12}>
              <LotGagnant />
            </Grid>
          )}
          {handlePermission(user?.perms, PERMISSIONS.VIEW_GRAPHE) && (
            <>
              <Grid item xs={12} md={6}>
                <AppAreaFiches />
              </Grid>
              <Grid item xs={12} md={6}>
                <AppTirage />
              </Grid>
              <Grid item xs={12} md={6}>
                <AppGain />
              </Grid>
              <Grid item xs={12} md={6}>
                <AppGPC />
              </Grid>
              <Grid item xs={12} md={6}>
                <AppLotto />
              </Grid>
            </>
          )}
        </Grid>
      </Container>
    </Page>
  );
}
